@-webkit-keyframes pop-in-big {
    0% {
        -webkit-transform: scale(0) translateZ(0);
        transform: scale(0) translateZ(0);
        opacity: 0;
        margin-top: -56px;
    }
    100% {
        -webkit-transform: scale(1) translateZ(0);
        transform: scale(1) translateZ(0);
        opacity: 1;
        margin-top: 30px;
    }
}

@keyframes pop-in-big {
    0% {
        -webkit-transform: scale(0) translateZ(0);
        transform: scale(0) translateZ(0);
        opacity: 0;
        margin-top: -56px;
    }
    100% {
        -webkit-transform: scale(1) translateZ(0);
        transform: scale(1) translateZ(0);
        opacity: 1;
        margin-top: 30px;
    }
}

