.app-notice {
    position: relative;
    -webkit-animation: pop-in-big 700ms ease 2s both;
    animation: pop-in-big 700ms ease 2s both;
    background: #edc53f;
    color: white;
    padding: 18px;
    margin-top: 30px;
    height: 56px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 3px;
}

.app-notice:after {
    content: "";
    display: block;
    clear: both;
}

.app-notice .notice-close-button {
    float: right;
    font-weight: bold;
    cursor: pointer;
    margin-left: 10px;
    opacity: 0.7;
}

.app-notice p {
    margin-bottom: 0;
}

.app-notice,
.app-notice p {
    line-height: 20px;
}

.app-notice a {
    color: white;
}

@media screen and (max-width: 520px) {
    .app-notice {
        -webkit-animation: pop-in-small 700ms ease 2s both;
        animation: pop-in-small 700ms ease 2s both;
        margin-top: 10px;
        height: 76px;
    }
}


